.reviews {
  padding-bottom: 51px;

  background-image: url("../img/bg-zigzag-line.svg");
  background-repeat: repeat-x;
  background-position: 0 100%;
  background-size: 320px 6px;

  word-break: break-word;
}

.reviews__title {
  margin-bottom: 25px;
}

.reviews__list {
  @include list-reset;
}

.reviews__item {
  margin-bottom: 30px;
}

.reviews__blockquote {
  margin: 0;
}

.reviews__text {
  margin-top: 0;
  margin-bottom: 26px;
}

.reviews__control {
  width: 50%;
  height: 52px;
  padding: 0;

  vertical-align: middle;

  background-color: $white;
  background-repeat: no-repeat;
  border: solid 1px $border-grey;

  cursor: pointer;

  &:hover,
  &:focus {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }

  &--prev {
    background-image: url("../img/icon-left-arrow.svg");
    background-position: 47% 50%;
    border-right: none;
  }

  &--next {
    background-image: url("../img/icon-right-arrow.svg");
    background-position: 48% 50%;
  }
}

.reviews__author-name {
  font-style: normal;
  font-size: 17px;
  line-height: 24px;
  font-weight: bold;
  color: $black;
}

.reviews__author-nickname {
  font-style: normal;
  font-size: 17px;

  word-break: break-word;
}

.reviews__button-wrapper {
  font-size: 0;
}

.reviews__write {
  margin-top: 37px;
  padding: 15px 10px;

  font-size: 18px;
  line-height: 20px;
}

@media (min-width: $tablet-width) {
  .reviews {
    position: relative;

    padding-top: 79px;
    padding-bottom: 132px;

    background-image: url("../img/bg-zigzag-line.svg");
    background-repeat: repeat-x;
    background-position: 0 100%;
    background-size: 768px 15px;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/bg-zigzag-line.svg");
    }

    .reviews__wrapper {
      position: relative;

      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: flex-start;

      z-index: 2;
    }

    &::before {
      content: "";

      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;

      width: 350px;
      height: 457px;

      background-color: #f2f2f2;
      background-image: url("../img/ignored_by_webp/review@1x.jpg");
      background-repeat: no-repeat;
      background-position: 0 0;

      @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
        background-image: url("../img/ignored_by_webp/review@2x.jpg");
      }
    }
  }

  .reviews__title {
    max-width: 450px;
    margin-bottom: 0;
  }

  .reviews__list {
    order: 1;
    margin-top: 45px;
  }

  .reviews__item {
    margin-bottom: 0;
  }

  .reviews__text {
    margin-bottom: 55px;
  }

  .reviews__author-name {
    font-size: 18px;
  }

  .reviews__author-nickname {
    padding-right: 175px;
  }

  .reviews__button-wrapper {
    position: absolute;
    right: 19px;
    bottom: 1px;

    display: flex;
    justify-content: space-between;
    width: 137px;
  }

  .reviews__control {
    width: 50px;
    height: 52px;

    border: none;
  }

  .reviews__write {
    width: 26.2%;
    margin: 0;

    font-size: 17px;
  }
}

@media (min-width: $desktop-width) {
  .reviews {
    padding-top: 70px;
    padding-bottom: 186px;

    background-repeat: no-repeat;

    background-position: 50px 100%;

    background-size: 1050px 23px;

    &::before {
      left: 50px;
    }
  }

  .reviews__title {
    max-width: 800px;
    padding-left: 16.25%;
  }

  .reviews__list {
    margin-top: 45px;
    padding-left: 16.67%;
  }

  .reviews__text {
    margin-bottom: 36px;
    padding-right: 80px;
  }

  .reviews__author-name {
    font-size: 17px;
  }

  .reviews__button-wrapper {
    bottom: 2px;
  }

  .reviews__write {
    width: 16.67%;
    margin-top: 8px;
  }
}
